:root {
  --background: #ffffff;
  --foreground: #1a1a1a;
  --gradient-primary: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  --gradient-primary-light: linear-gradient(135deg, #a8b5ff 0%, #b19cd9 100%);
  --gradient-primary-dark: linear-gradient(135deg, #4c63d2 0%, #5a4b7c 100%);
  --gradient-secondary: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  --gradient-accent: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  --gradient-success: linear-gradient(135deg, #43e97b 0%, #38f9d7 100%);
  --gradient-warning: linear-gradient(135deg, #fa709a 0%, #fee140 100%);
  --gradient-error: linear-gradient(135deg, #ff6b6b 0%, #ffa726 100%);
  --gradient-neutral: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
  --gradient-dark: linear-gradient(135deg, #2d3748 0%, #1a202c 100%);
  --glass-bg: rgba(255, 255, 255, 0.25);
  --glass-border: rgba(255, 255, 255, 0.18);
  --glass-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  --shadow-soft: 0 2px 15px -3px rgba(0, 0, 0, 0.07), 0 10px 20px -2px rgba(0, 0, 0, 0.04);
  --shadow-medium: 0 4px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  --shadow-strong: 0 10px 40px -10px rgba(0, 0, 0, 0.15), 0 2px 10px -2px rgba(0, 0, 0, 0.05);
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0f0f23;
    --foreground: #e2e8f0;
    --gradient-primary: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    --gradient-primary-light: linear-gradient(135deg, #8b9cff 0%, #9d7bc7 100%);
    --gradient-primary-dark: linear-gradient(135deg, #3c4fd8 0%, #4a3b6e 100%);
    --gradient-secondary: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
    --gradient-accent: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
    --gradient-success: linear-gradient(135deg, #43e97b 0%, #38f9d7 100%);
    --gradient-warning: linear-gradient(135deg, #fa709a 0%, #fee140 100%);
    --gradient-error: linear-gradient(135deg, #ff6b6b 0%, #ffa726 100%);
    --gradient-neutral: linear-gradient(135deg, #2d3748 0%, #1a202c 100%);
    --gradient-dark: linear-gradient(135deg, #1a202c 0%, #0f0f23 100%);
    --glass-bg: rgba(45, 55, 72, 0.25);
    --glass-border: rgba(255, 255, 255, 0.1);
    --glass-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.5);
  }
}
